os":"fade-right"}},[_c('h5',[_vm._v("Figura 23")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v(" Digestor anaerobio para lodos")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/39.png"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Equimar (s.f.). http://equimar.mx/our-services/digestores-anaerobios-para-lodos/ ")])])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Etapas de la conversin biolgica ","icono":require('@/assets/curso/temas/tema2/37.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-md-0"},[_c('p',[_vm._v("La conversin biolgica de la materia orgnica de los lodos parece que se produce en tres etapas. El primer paso del proceso comporta la transformacin por va enzimtica (hidrlisis) de los compuestos de alto peso molecular en compuestos que puedan servir como fuentes de energa y de carbono celular. El segundo paso (acidognesis) implica la conversin bacteriana de los compuestos producidos en la primera etapa en compuestos intermedios identificables de menor peso molecular. El tercer paso (metanognesis) supone la conversin bacteriana de los compuestos intermedios en productos finales ms simples, principalmente metano y dixido de carbono. En un digestor, la conversin de los lodos orgnicos y de los residuos se lleva a cabo mediante la accin conjunta de diferentes organismos anaerobios (Aguamarket, 2017). ")])]),_c('div',{staticClass:"col-lg-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/40.png"),"alt":""}})])])]),_c('div',{staticClass:"py-4 py-md-5",attrs:{"titulo":"Hidrolizacin","icono":require('@/assets/curso/temas/tema2/37.svg')}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Un grupo de microorganismos se ocupa de la hidrolizacin de los polmeros orgnicos y de los lpidos para formar elementos estructurales bsicos como los monosacridos, los aminocidos y los compuestos relacionados con estos. Un segundo grupo de bacterias anaerobias fermenta los productos de la descomposicin para producir cidos orgnicos simples, de los que el que se presenta con mayor frecuencia en los digestores orgnicos es el cido actico. Este grupo de microorganismos, que reciben el nombre de no metanognicos, est formado por bacterias facultativas y anaerobias estrictas, aunque de forma colectiva se conocen como bacterias \"formadoras de cidos (Aguamarket, 2017). ")])]),_c('div',{staticClass:"col-lg-6"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/41.png"),"alt":""}})])])])]),_vm._m(37),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Se trata de un sistema de depuracin biolgico aerobio cuyo funcionamiento se basa en hacer circular a travs de un medio poroso, aire y agua residual. Estn configurados en forma de cultivo fijo no sumergido y compuestos por un lecho, normalmente de gran dimetro, que puede estar formado por materiales naturales cantos rodados, coque metalrgico, escoria o antracita- o plsticos, y en cuya superficie se aplican de forma uniforme los lquidos residuales por medio de pulverizadores, y en forma de lluvia (SMA, 2015), ver figuras 24 y 25.")]),_vm._m(38),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("En la parte inferior del lecho se sitan el sistema de drenaje para la salida del efluente del proceso y el sistema de entrada de aire para la ventilacin. Esta puede llevarse a cabo de forma natural, lo que exige para un correcto funcionamiento diferencias de temperatura superiores a 2C entre el aire -agua o forzada, suele emplearse solo cuando el tamao del filtro es demasiado grande y no se puede garantizar la ventilacin natural. Cabe destacar que el lecho debe permanecer aireado y con un caudal suficiente de percolacin, que permita arrastrar las fracciones de biopelcula erosionadas o desprendidas, y sin saturacin de agua. De esta forma, se puede prevenir, en caso de cargas altas, la formacin de charcos en su superficie (SMA, 2015), ver figura 26. ")]),_vm._m(39),_vm._m(40),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Los contactores biolgicos rotativos estn basados en los procesos biolgicos aerobios. Estos requieren un contacto ntimo entre el agua residual, la biomasa activa y el oxgeno. Consisten en una serie de placas o discos colocados en un eje horizontal que giran lentamente dentro del tanque que contiene el agua residual, con un 49% de la superficie sumergida. Sobre la superficie de los discos se fija la biomasa que se encuentra alternativamente en contacto con el agua residual y el aire. Esto posibilita la ingestin de la materia orgnica por parte de los microorganismos y su posterior degradacin en presencia del oxgeno del aire (Cyclusid, 2020), ver figura 27. ")]),_vm._m(41),_vm._m(42),_vm._m(43),_vm._m(44),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Son reactores discontinuos en los que el agua residual se mezcla con un lodo biolgico en un medio aireado. Se trata de un proceso que se combina en un mismo tanque: aeracin/reaccin y clarificacin. La tecnologa de los reactores biolgicos secuenciales es una variante optimizada de la tecnologa convencional de lodos activados. Se basa en el uso de un solo reactor que opera en forma discontinua secuencial. El sistema de los reactores biolgicos secuenciales SBR consta de al menos cuatro procesos cclicos: llenado, aireacin, anoxia, decantacin y vaciado, tanto de efluente como de los lodos. Esta tecnologa es capaz de tolerar variaciones de carga y caudal y genera como producto lodos estabilizados, siendo en ocasiones, la tecnologa ms apropiada para la industria, ver figura 30 y 31. ")]),_vm._m(45),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("Se debe considerar que el tratamiento de aguas residuales mediante SBR se lleva a cabo en secuencia en 4 etapas: ")]),_vm._m(46),_vm._m(47),_c('p',[_vm._v("Un filtro verde consiste en una superficie de terreno donde se establece una plantacin forestal 