, la decantacin y concentracin del lodo se realiza en un estanque independiente. La digestin con oxgeno puro es una modificacin del proceso de digestin aerobia en el que se sustituye el aire por oxgeno puro. El lodo que resulta es parecido al lodo que se obtiene en los procesos de digestin aerobia convencionales. La digestin aerobia termfila representa un refinamiento adicional del proceso de digestin aerobia. Este proceso puede permitir conseguir altos rendimientos de eliminacin de la fraccin biodegradable (superiores al 80%) en tiempos de detencin cortos (3 a 4 das), mediante la accin de bacterias termfilas a temperaturas entre 25 y 50 C superiores a la temperatura ambiente (Aguamarket, 2017). ")])]),_c('div',{staticClass:"col-lg-7 col-8"},[_c('div',{staticClass:"d-lg-block d-none"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 21")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v(" Tanque aerobio TAE ")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/35.jpg"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Ajover (s.f.). http://www.ajover.co/wp-content/uploads/2019/02/fichatecnicasistemasepticoecoajoverajover.pdf")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta color-primario rounded-pill d-inline-block m-auto p-3 mb-4"},[_c('h4',{staticClass:"mb-0"},[_vm._v("Digestor anaerobio")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-6 mb-lg-0 mb-3",attrs:{"data-aos":"fade-right"}},[_c('p',[_vm._v("En este proceso se produce la descomposicin de la materia orgnica e inorgnica en ausencia de oxgeno molecular. Sus principales aplicaciones han sido y siguen siendo hoy en da, la estabilizacin de lodos concentrados producidos en el tratamiento del agua residual y de determina