iana crece espontneamente y los slidos en suspensin a la salida del biodisco disminuyen. Si la carga disminuye, la biomasa se desprende y aumentan los slidos en el efluente al decantador secundario. No es necesario controlar la concentracin del licor de mezcla, ya que el fango extrado es un desecho del proceso y ha de eliminarse como mejor convenga. ")])]),_c('div',{staticClass:"col-lg-6",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"d-lg-block d-none"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-right"}},[_c('h5',[_vm._v("Figura 28")]),_c('br'),_c('span',{staticClass:"fst-italic"},[_vm._v(" Esquema del sistema de biodiscos ")])]),_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/47.jpg"),"alt":""}}),_c('figcaption',[_vm._v("Nota. Tomado de Delgado (2018). http://revistas.uni.edu.pe/index.php/tecnia/article/view/545/1076 ")])])])]),_c('h4',[_vm._v("Ruidos y otras variables ")]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-right"}},[_vm._v("El nivel de ruidos es muy bajo, factor que es muy importante tanto ambientalmente como por higiene y salud del personal. Las dimensiones de los depsitos de oxidacin son menores que los utilizados por otros procesos. El rendimiento del proceso es ms estable en pocas fras, debido al hecho de permanecer cubiertos los tanques. El consumo energtico es muy reducido y a igualdad de resultados, comparndolo con un sistema de fangos activos, el consumo resulta ser la tercera parte Hidrometalica (s.f.). ")]),_c('h4',[_vm._v("El funcionamiento de los biodiscos")]),_c('p',[_vm._v("Se divide en cuatro etapas, ver figura 29. ")]),_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"rounded-2 p-5",staticStyle:{"background-color":"#dee9f0"}},[_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido",attrs:{"data-aos":"fade-rig