      h1 Sistemas para el tratamiento fsico, qumico y biolgico del agua residual
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3(data-aos="fade-right")
        p El control de las aguas residuales se basa mayormente en tratamientos fsicos, qumicos y biolgicos que depuran las concentraciones contaminantes presentes en un vertimiento originado de manera antrpica. Es por ello por lo que existen las plantas de tratamiento de aguas residuales PTAR, las cuales cuentan con un tren de tratamiento dividido por unidades primarias, secundarias y terciarias. En cada una de estas unidades de tratamiento se eliminan las concentraciones contaminantes de acuerdo con su diseo y funcionamiento. Estos a su vez cuentan con una eficiencia de tratamiento, que es la que permite entregar en el efluente el agua en buenas condiciones.
          br
          br
          |Es as como los tratamientos fsicos se concentran en remover principalmente slidos, partiendo del diseo de su estructura. Por otro lado, el tratamiento qumico se da a travs de la dosificacin de un agente o reactivo que de acuerdo con las necesidades de tratamiento del agua puede coadyuvar a sedimentar slidos, desinfectar o hasta precipitar metales pesados. Por ltimo, el tratamiento biolgico es el encargado de remover la carga orgnica que contiene el agua residual, que por lo general por accin de ciertos microorganismos depuran el agua residual en su proceso metablico y de digestin.
