    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Pretratamientos
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3(data-aos="fade-right")
        p Los pretratamientos y los que se presentarn enseguida son los ms representativos, de acuerdo con su clasificacin. 
          br
          br
          |Los pretratamientos engloban a aquellos procesos que se sitan a la entrada de la planta depuradora para eliminar residuos slidos, arenas y grasas, que de no ser separados daaran mecnicamente los equipos de las siguientes fases del tratamiento y sedimentaran en las tuberas y conductos de la instalacin, obstruyndolos o bien produciran prdida de eficacia (p.e. grasas en el reactor biolgico). En todos los procesos se eliminan los contaminantes del agua residual por medios fsicos. 
      .col-lg-5.col-8(data-aos="fade-left"): img(src='@/assets/curso/temas/tema2/2.png', alt='')
    .mn.p-5(style="background-color: #fef7e2 ").mb-5
      .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
        h4.mb-0 Desbaste
      .row.justify-content-center.mb-5
        .col-lg-7.mb-lg-0.mb-3(data-aos="fade-right")
          p Es una operacin en la que se eliminan los slidos de mayor tamao del agua residual. El agua se hace pasar por rejas o tamices y tiene como objetivo separar todos aquellos materiales de tamao excesivamente grueso que, adems de representar por s una forma de contaminacin (slidos en suspensin), pueden daar u obstaculizar las fases sucesivas del tratamiento, ver figura 1. 
        .col-lg-5.col-8(data-aos="fade-left")
          .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Figura 1
            br
            span.fst-italic Rejas de desbaste 
          img(src='@/assets/curso/temas/tema2/3.png', alt='')
      .tarjeta.p-5.mb-4(style="background-color: #dee9f0 ")   
        h4 Tipos de rejas de desbaste segn separacin entre barrotes 
        p.mb-0 Las rejas de desbaste pueden clasificarse segn el espacio libre que hay entre los barrotes. Esta distancia libre marcar qu tipo de slidos quedarn atrapados y qu tipo podrn pasar al sistema de tratamiento de agua (Deagua, 2020). 
      .row.justify-content-center.mb-3
        .col-lg-6.mb-lg-0.mb-3
          .tarjeta.p-5.mb-4(style="background-color: #dee9f0 ").h-100
            h4 Rejas de desbaste de gruesos
            p Son rejas de desbaste para filtrar slidos grandes; estn compuestas por un recipiente y una reja con una separacin entre barrotes de 12mm a 25mm, retienen la mayora de los slidos grandes y dejan pasar los slidos ms finos para que puedan ser tratados (Deagua, 2020), ver figura 2. 
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 2
              br
              span.fst-italic Rejas de desbaste
            figure
              img(src='@/assets/curso/temas/tema2/4.png', alt='')
        .col-lg-6.mb-lg-0.mb-3
          .tarjeta.p-5.mb-4(style="background-color: #dee9f0 ").h-100
            h4 Rejas de desbaste de finos
            p Son rejas de desbaste para filtrar slidos finos, estn compuestas por un recipiente y una reja con una separacin entre barrotes de 6mm a 12mm, retienen los slidos grandes y la mayora de los slidos finos, y solo dejan pasar los fangos ms finos para que puedan ser tratados (Deagua, 2020), ver figura 3. 
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 3
              br
              span.fst-italic Rejas de desbaste finos
            figure
              img(src='@/assets/curso/temas/tema2/5.png', alt='')
      .row.justify-content-center
        .col-lg-6.mb-lg-0.mb-3
          .tarjeta.p-5.mb-4(style="background-color: #dee9f0 ").h-100
            h4 Las rejillas tambin operan de manera manual 
            p Las rejas de desbaste de limpieza manual son las ms utilizadas por su economa, ya que el usuario deber limpiar peridicamente la reja manualmente con un rastrillo y eliminar los slidos atrapados. Las rejas de desbaste de limpieza automtica se caracterizan por su funcionamiento mecnico. Atrapan los slidos ms gruesos y a travs de un cepillo o brazo hidrulico las envan a un contenedor para su reciclado (Deagua, 2020). 
        .col-lg-6.mb-lg-0.mb-3
          .tarjeta.p-5.mb-4(style="background-color: #dee9f0 ").h-100
            h4 Cada tipo de reja de desbaste est compuesta de:
            p Reja de desbaste de gruesos, recipiente, tapa de registro, tubera de entrada y de salida, reja con separacin entre barrotes de 12-25mm, reja de desbaste de finos, recipiente, tapa de registro, tubera de entrada y de salida y reja con separacin entre barrotes de 6-12mm (Deagua, 2020, ver figura 4. 
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 4
              br
              span.fst-italic Desbastes 
            figure
              img(src='@/assets/curso/temas/tema2/6.jpg', alt='Texto alternativo descriptivo: Grfica que muestra una accin de desbaste mediante el uso de rejas de gruesos, rejas de finos, contenedores y cintas transportadoras.')
              figcaption Nota. Tomado de Deagua (2020). http://blogdeagua.es/reja-de-desbaste/
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Desarenado 
    p.mb-5(data-aos='fade-right') El proceso de desarenado se utiliza para separar la arena, la grava, etc., arrastrada en suspensin por el efluente. Esta arena origina depsitos en canales y tuberas, abrasin y desgaste sobre los elementos mecnicos en movimiento y dificulta la eliminacin y digestin de los lodos separados en los tanques de sedimentacin, al aumentar su densidad (Cyclusid depuracin industrial, s.f.), ver figura 5.
    .row.justify-content-center.mb-5
      .col-lg-5.col-8.mb-lg-0.mb-3
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 5
          span Desarenador
        img(src='@/assets/curso/temas/tema2/7.jpg', alt='')
        figcaption #[b Nota.] Desarenador Santa Fe de la Laguna, Quiroga Michoacn. Tomado de Rivas (s.f.). https://sswm.info/es/gass-perspective-es/tecnologias-de/tecnologias-de-saneamiento-del-sistema/tratamiento-semi/tecnolog%C3%ADas-de-pretratamiento  
      .col-lg-7
        p De acuerdo con la empresa mexicana Domos agua (2018), los tipos de desarenadores se dividen en:
        .tarjeta.p-4(style="background-color: #eaf8f2").mb-4
          p.mb-0 #[b  Desarenadores de flujo horizontal:] consiste en reducir la velocidad de flujo del agua, con el fin de que las partculas slidas que se encuentran suspendidas se decanten y se depositen en el fondo. Esto se logra ensanchando el canal en un tramo, en donde existirn canales paralelos que ayuden en la limpieza de las partculas depositadas. Se debe medir el caudal y la velocidad mediante un canal parshall para mantener controlado el flujo de agua.
        .tarjeta.p-4(style="background-color: #eaf8f2").mb-4
          p.mb-0 #[b  Desarenadores de flujo vertical:] estn compuestos por varios compartimentos, de tal manera que d inicio a fin y, la velocidad sea decreciente, a travs de estos compartimentos el agua fluye de forma vertical. En estos desarenadores se lleva a cabo la floculacin (proceso qumico en el que se aaden sustancias denominadas floculantes para hacer ms eficiente la decantacin de las partculas suspendidas). Los materiales con los que se construyen las paredes de los compartimentos son concreto, asbesto o madera y la profundidad de estos es de tres a cuatro metros. 
        .tarjeta.p-4(style="background-color: #eaf8f2").mb-4
          p.mb-0 #[b  Desarenadores de flujo inducido:] estos desarenadores tienen forma rectangular y en ellos se inyecta aire mediante aireadores tipo turbina para producir el movimiento del agua en forma de espiral, lo cual produce la decantacin de los slidos suspendidos. Este proceso tambin favorece la deposicin de la materia orgnica. Posteriormente, se lleva a cabo la separacin de las arenas de forma manual o mecanizada, de acuerdo con el tamao de la planta, ver figura 6. 
    .row.justify-content-center.mb-5
      .col-lg-8
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 6
          br
          span.fst-italic Desarenadores 
        img(src='@/assets/curso/temas/tema2/8.jpg', alt='Ilustracin que muestra un desarenador y sus partes: canal de llegada, compuerta de admisin, cmara de sedimentacin, canal directo, compuerta de lavado, vertedero, canal de lavado, canal de salida.')
        figcaption Nota. Tomado de Universidad de Loja (2015).  https://vdocuments.mx/calculo-del-desarenador.html  
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Desengrasado
    p.mb-5(data-aos='fade-right') Despus de cualquiera de los tres procesos de desarenado se tiene que efectuar un desengrasado para separar las grasas y aceites de los canales, previniendo que los siguientes procesos se vean afectados. Aunque existen desarenadores que llevan el proceso de desengrasado de manera conjunta con el desarenado mediante aire comprimido (Domos agua, 2018), ver figura 7.
    .row.justify-content-center.mb-5
      .col-lg-6.col-8(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 7
          br
          span.fst-italic Desarenado, desengrasado y elevacin 
        img(src='@/assets/curso/temas/tema2/9.jpg', alt='Ilustracin que muestra el proceso de desarenado, desengrasado y elevacin desde el canal de recogida de arenas hasta el canal de recogida de grasas.')
        figcaption Nota. Tomado de Educarex (2007). 
      .col-lg-6(data-aos="fade-right").mb-lg-0.mb-3
        p De acuerdo con la empresa Cyclusid el desengrasado consiste en la separacin de las grasas y aceites arrastrados por el agua residual. Las grasas en las aguas residuales crean numerosos problemas en el proceso de depuracin, entre los que se destacan:
        div(style="background-color: #fdf8e4 ").p-4
          ul.lista-ul--color.fw-bold
            li
              i.fas.fa-check
              | Adhesin a aparatos, conductos o depsitos, dificultando la depuracin. 

            li
              i.fas.fa-check
              | Obstruccin de las rejillas finas. 

            li
              i.fas.fa-check
              | Formacin de una capa en la superficie de los decantadores, que dificulta la sedimentacin al atraer hacia arriba pequeas partculas de materia orgnica. 
            li
              i.fas.fa-check
              | Dificulta la correcta aireacin en la depuracin de fangos activos.
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Homogenizacin
    .row.justify-content-center.mb-5
      .col-lg-6(data-aos="fade-right").mb-lg-0.mb-3
        p  La situacin ms habitual es que la entrada de agua residual a la planta depuradora sea variable en el tiempo, tanto en lo que respecta al caudal como a la carga contaminante. Estas variaciones, especialmente las descargas, dificultan el correcto desarrollo de los tratamientos, ya sean de tipo fisicoqumico o biolgico. Para solventar tales problemas se puede recurrir a la igualacin de caudales y/o a la homogeneizacin de la concentracin de contaminantes, es decir, optimizar las condiciones operativas de las fases siguientes (Cyclusid depuracin industrial, s.f.), ver figura 8.
      .col-lg-6.col-8(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 8
          br
          span.fst-italic Tanque de homogenizacin
        img(src='@/assets/curso/temas/tema2/10.jpg', alt='')
        figcaption Nota. Tomado de Tecnologa Assul, S.A. De C.V.(s.f.). 
    Separador 
    #t_2_2.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.2 Tratamientos primarios del agua residual 
    .row.justify-content-center.mb-5
      .col-lg-4(data-aos="fade-right")
        p Son aquellos que eliminan los slidos en suspensin presentes en el agua. Los principales procesos fisicoqumicos que pueden ser incluidos en el tratamiento primario son: 
      .col-lg-2.mb-lg-0.mb-3.col-8
