      h4.mb-0 Sedimentacin
    .row.justify-content-center.mb-5
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
        p Es un proceso de separacin por gravedad que hace que una partcula ms densa que el agua tenga una trayectoria descendente, depositndose en el fondo del sedimentador. Est en funcin de la densidad del lquido, del tamao, del peso especfico y de la morfologa de las partculas (Acuatecnia S.A.S, 2018), ver figuras 9 y 10. 
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 9 
          br
          span.fst-italic Sedimentador circular
        img(src='@/assets/curso/temas/tema2/14.jpg', alt='')
        figcaption Nota. Tomado de Educarex (2007).  https://iesmunoztorrero.educarex.es/web/aguaserena/activid/edar/4.decantacion-pri.gif  
      .col-lg-4(data-aos="fade-left")
        .tarjeta.p-3(style="background-color: #fdf8e4")
          .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Figura 10
            br
            span.fst-italic  Decantacin primaria
          img(src='@/assets/curso/temas/tema2/15.png', alt='')
          figcaption Nota. Tomado de Tratamiento del agua (s.f.). http://www.tratamientodelagua.com.mx/wp-content/uploads/Sedimentador-circular.png  
    p.mb-5(data-aos='fade-right') De acuerdo con Maldonado (s.f.), los tipos de sedimentacin se pueden clasificar como:
    .row.justify-content-center.mb-5
      .col-lg-3.d-none.d-lg-block(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/16.svg', alt='AvatarTop')
      .col-lg-9.col-12(data-aos="fade-left")
        .tarjeta.w-100(style="background-color: #e2eacb").p-4.mb-3
          p.mb-0 #[b  Sedimentacin de partculas discretas:] son aquellas partculas que no cambian de caractersticas (forma, tamao, densidad) durante la cada. Se denomina sedimentacin o sedimentacin simple al proceso de depsito de partculas discretas. Este tipo de partculas y esta forma de sedimentacin se presentan en los desarenadores, en los sedimentadores y en los presedimentadores como paso previo a la coagulacin en las plantas de filtracin rpida y tambin en sedimentadores como paso previo a la filtracin lenta.  
        .tarjeta.w-100(style="background-color: #e2eacb").p-4.mb-3
          p.mb-0 #[b  Sedimentacin de partculas floculentas:] partculas floculentas son aquellas producidas por la aglomeracin de partculas coloides desestabilizadas a consecuencia de la aplicacin de agentes qumicos. A diferencia de las partculas discretas, las caractersticas de este tipo de partculas forma, tamao, densidad s cambian durante la cada; se denomina sedimentacin floculenta o decantacin al proceso de depsito de partculas floculentas. Este tipo de sedimentacin se presenta en la clarificacin de aguas como proceso intermedio entre la coagulacin-floculacin y la filtracin rpida. 
        .tarjeta.w-100(style="background-color: #e2eacb").p-4.mb-3
          p.mb-0 #[b  Sedimentacin por cada libre e interferida:] cuando existe una baja concentracin de partculas en el agua, estas se depositan sin interferir y se denomina a este fenmeno cada libre. En cambio, cuando hay altas concentraciones de partculas se producen colisiones que las mantienen en una posicin fija y ocurre un depsito masivo en lugar de individual. A este proceso de sedimentacin se le denomina depsito o cada interferida o sedimentacin zonal. Cuando las partculas ya en contacto forman una masa compacta que inhibe una mayor consolidacin se produce una compresin o zona de compresin. Este tipo de sedimentacin se presenta en los concentradores de lodos de las unidades de decantacin con manto de lodos. 
    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta.w-100(style="background-color: #EAF8F2").p-4.mb-3
          p #[b  Decantacin:] es un mtodo fsico para separar componentes de distinta densidad, de modo que el ms denso se ubica en el fondo del decantador, en virtud de la fuerza de gravedad. De este modo, el agua clara o clarificada se ubicar en la superficie.
          .row.justify-content-center.mb-5
            .col-lg-10
              .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                h5 Figura 11 
                br
                span.fst-italic Decantacin
              img(src='@/assets/curso/temas/tema2/17.jpg', alt='')
              figcaption Nota. Tomado de El agua potable (s.f.). http://www.elaguapotable.com/decantacion.htm 
    p.mb-5(data-aos='fade-right') #[b  Los tipos de sedimentadores se clasifican por:]  su forma, la cual puede ser cuadrada, rectangular o redonda. En virtud del escurrimiento que proporcionan tambin pueden tipificarse como sedimentadores de escurrimiento vertical o de escurrimiento radial. En general, los tipos de sedimentadores son los que presenta la siguiente figura:

    .bg-1.py-4.px-5.mb-5
      .titulo-sexto.color-acento-contenido(data-aos='fade-right')
        h5 Figura 12
        br
        span.fst-italic Tipos de sedimentadores 
      img(src='@/assets/curso/temas/tema2/18.svg', alt='Diagrama que enuncia los tipos de sedimentadores: Estticos, que son de flujo vertical y horizontal, los dinmicos, que son de manto de lodos y los luminares, que pueden ser de lminas horizontales o inclinadas.')
      figcaption Nota. Tomado de Pedraza (2020).
    p.mb-5(data-aos='fade-right') #[b  Los sedimentadores y su clasificacin: ]
    p.mb-5(data-aos='fade-right') Los sedimentadores pueden ser tratamientos primarios o secundarios dependiendo de su ubicacin dentro del tren de tratamiento, esto depende de las necesidades de tratamiento de acuerdo con las caractersticas del agua residual inicial.
      br
      br
      |El siguiente esquema ilustra las diferencias entre ambos sedimentadores:
    .row.justify-content-center.mb-5
      .col-lg-10.mb-lg-0.mb-3(data-aos="fade-right") 
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 13
          br
          span Sedimentadores
        img(src='@/assets/curso/temas/tema2/19.svg', alt='')
        figcaption Nota. Tomado de Pedraza (2020).
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Flotacin
    .row.justify-content-center.mb-5
      .col-lg-6.col-12(data-aos="fade-right")
        p Es un proceso fsico fundamentado en la diferencia de densidades. La flotacin permite separar la materia slida o lquida de menor densidad que la del fluido, por ascenso de esta hasta la superficie del fluido, ya que en este caso las fuerzas que tiran hacia arriba (rozamiento y empuje del lquido) superan a la fuerza de la gravedad. Se generan pequeas burbujas de gas (aire), que se asociarn a las partculas presentes en el agua y sern elevadas hasta la superficie, donde son arrastradas y sacadas del sistema (Acuatecnia S.A.S., 2018), ver figura 14.  
      .col-lg-6.col-8(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 14
          br
          span.fst-italic  Sistema de flotacin
        img(src='@/assets/curso/temas/tema2/21.png', alt='')
        figcaption Nota. Tomado de Aguas Sigma (2016). https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSJB-E5CFFX1Cehh4NVp4a-E9Rer2hlmr0MSMTMPRjVTKW2M3U9 
    .tarjeta.bg-slyde.py-5.px-3.mb-5
      SlyderA(tipo='b').slyder-2
        .row.mx-3
          .col-lg-6.mb-4.mb-md-0
            h4 En qu consiste la flotacin?
            p En unir las burbujas de aire al material suspendido, causando una reduccin neta del peso especfico. Las burbujas de tamao micromtrico se producen por disolucin del aire en el agua residual a presiones elevadas, seguida por su posterior liberacin a la presin atmosfrica. Cuando la presin se reduce a las condiciones atmosfricas el aire disuelto presente en exceso hasta la saturacin es liberado en forma de burbujas extremadamente finas que se adhieren a las materias en suspensin.
          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema2/22.png', alt='Texto que describa la imagen')
        .row.mx-3
          .col-lg-6.mb-4.mb-md-0
            h4 Qu sistema se usa para la flotacin? 
            p Los slidos, grasas y flculos flotan hacia la parte superior de la celda de flotacin por aire disuelto y se eliminan mediante un dispositivo de barrido. Para el efecto de flotacin se utiliza un sistema de presurizacin de agua recirculada. Dentro de ellos podemos encontrar el sistema de flotacin por aire disuelto que se ha convertido en el sistema de clarificacin de aguas ms verstil (Aguas Sigma, 2016). 
          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema2/23.png', alt='Texto que describa la imagen')
        .row.mx-3
          .col-lg-6.mb-4.mb-md-0
            h4  Qu permiten solucionar los sistemas de flotacin? 
            p Permiten dar solucin a gran cantidad de necesidades como: 
              br
              br
              | Pretratamiento de aguas residuales: separacin de grasas y  slidos en suspensin. 
              br
              | Tratamientos fsico-qumicos. 
              br
              | Clarificacin de aguas de reactores biolgicos.  
              br
              | Espesadores de lodos. 
              br
              | Pretratamiento de aguas potables - separacin de slidos en   suspensin. 
              br
              | Pretratamiento en plantas de osmosis inversa - separacin de  algas. 
              br
              | Pretratamiento en aguas de proceso. 
              br
              | Recuperacin de fibra en plantas papeleras. 
              br
              br
              |Ver figura que explica el proceso de flotacin 
          .col-lg-6
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 15
              br
              span.fst-italic Flotacin 
            figure
              img(src='@/assets/curso/temas/tema2/24.png', alt='Texto que describa la imagen')
              figcaption Nota. Tomado de El agua potable (s.f.). http://www.elaguapotable.com/decantacion.htm
        .row.mx-3
          .col-lg-6.mb-4.mb-md-0
            h4 Qu es el sistema de flotacin por aire inducido? 
            p Cuando el objetivo no es disponer de una gran eficiencia y calidad del agua y, la necesidad es disponer de un equipo robusto, fcil de utilizar, de bajo mantenimiento y econmico, existe el sistema de flotacin por aire inducido. En este el corazn del sistema es el inductor especial: el aireador impulsor gira creando un remolino y una depresin dentro de la celda que produce una aspiracin del aire a travs del eje hueco. El aire penetra en el lquido al llegar a la celda de aireacin, mediante grandes cantidades de micro burbujas. La mezcla rica en aire es liberada en la celda de flotacin facilitando la separacin de fases (Aguas Sigma, 2016). 
          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema2/25.png', alt='Texto que describa la imagen')
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Coagulacin-floculacin
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
        p En muchos casos parte de la materia en suspensin est formada por partculas de muy pequeo tamao, lo que conforma una suspensin coloidal. Estas suspensiones coloidales suelen ser muy estables, en muchas ocasiones debido a interacciones elctricas entre las partculas. Por tanto, tienen una velocidad de sedimentacin extremadamente lenta, por lo que hara inviable un tratamiento mecnico clsico. Una forma de mejorar la eficacia de todos los sistemas de eliminacin de materia en suspensin es la adicin de ciertos reactivos qumicos que, en primer lugar, desestabilicen la suspensin coloidal (coagulacin) y a continuacin favorezcan la floculacin de estas para obtener partculas fcilmente sedimentables (Acuatecnia S.A.S., 2018), ver figura 16. 
      .col-lg-6.col-8(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 16
          br
          span.fst-italic Floculadores 
        img(src='@/assets/curso/temas/tema2/26.png', alt='Texto que describa la imagen')
        figcaption Nota. Tomado de Eathisa (2018). http://eathisa.com/productos/floculadores/ 
    .tarjeta.bg-slyde.p-5.mb-5
      SlyderA(tipo='b')
        .row
          .col-lg-6.mb-4.mb-md-0
            h4 Objetivo principal 
            p De acuerdo con FRC Systems, el objetivo principal de un floculador es mezclar los coagulantes y los floculantes con el agua residual y formar slidos que puedan flotar. Los componentes principales incluyen una estructura metlica, una entrada, vlvulas de muestreo, vlvulas de inyeccin, tuberas y una salida. Las tuberas estn configuradas de manera ascendente y serpenteante para reducir la cantidad de espacio necesario para brindar un tiempo de reaccin qumica suficiente (FRC Systems, 2015). 
          .col-lg-6
            figure
              img(src='@/assets/curso/temas/tema2/27.svg', alt='Texto que describa la imagen')
        .row
          .col-lg-6.mb-4.mb-md-0
            h4 Mecanismo  funcionamiento 
            p Lo que ocurre dentro de un floculador es un concepto bastante simple, se bombea agua hacia del floculador, inmediatamente despus de la entrada se da inicio a la dosificacin qumica, generalmente coagulantes, mediante vlvulas de inyeccin. Ms adelante se inyecta un polmero floculante y se mezcla en las secciones subsiguientes del floculador. Algunas unidades incluyen un medidor de pH en lnea y puntos de inyeccin para agentes neutralizantes (FRC Systems, 2015), ver figura 17. 
          .col-lg-6
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 17
              br
              span.fst-italic Coagulantes y floculantes 
            figure
              img(src='@/assets/curso/temas/tema2/28.png', alt='Texto que describa la imagen')
              figcaption Nota. Tomado de Acoua tecnologa (2020). https://acquatecnologiaperu.com/producto/coagulantes-y-floculantes 
        .row
          .col-lg-6.mb-4.mb-md-0
            h4 Dosificacin qumica 
            p Se puede variar de acuerdo con el tipo de floculante, pues permite que las partculas coloidales desestabilizadas o las partculas formadas durante la etapa de coagulacin se ensamblen en agregados y luego se puedan separar del medio acuoso; entre ellos se encuentran los floculantes catinicos y los aninicos (FRC systems, 2015). 
          .col-lg-6
            h4 Preparacin de los floculantes aninicos 
            p Los floculantes aninicos son preparados a base de copolmeros de acrilamida y cido acrlico, con carga inica negativa. Se unen con cargas catinicas residuales sobre coagulantes adsorbidos a coloides coagulados. Su poder floculante intrnseco aumenta con el peso molecular. Las poliacrilamidas tienen el peso molecular ms alto en el intervalo de 10-20 millones. Otros polmeros presentan propiedades especficas para condiciones especficas (Ingeniera Liquid technologies, s.f.).
        .row
          .col-lg-6.mb-4.mb-md-0
            h4 Floculantes catinicos 
            p Se basan en copolmeros de poliacrilamida que proporcionan una amplia gama de ionicidades adems de los pesos moleculares necesarios para lograr el mximo rendimiento en cada aplicacin. De acuerdo con Acoua tecnologa (2020), puede haber varias cargas en un floculante. Hay polmeros catinicos, basados en el nitrgeno, polmeros aninicos, basados en los iones del carboxilato y los polianfolitos, que llevan cargas positivas y negativas.  
          .col-lg-6
            h4 Desestabilizacin de partculas 
            p Los principales coagulantes y floculantes utilizados para desestabilizar las partculas y producir floculantes en agua son: 
            .row.mb-5
              .col-lg-10
                div(style="background-color: #fef7e2").p-4
                  ul.lista-ul--color.fw-bold
                    li
                      i.fas.fa-check
                      |Sulfato de aluminio. 
                    li
                      i.fas.fa-check
                      |Aluminato de sodio. 
                    li
                      i.fas.fa-check
                      |Cloruro de aluminio.
                    li
                      i.fas.fa-check
                      |Cloruro frrico. 
                    li
                      i.fas.fa-check
                      |Sulfato frrico. 
                    li
                      i.fas.fa-check
                      |Sulfato ferroso.
                    li
                      i.fas.fa-check
                      |Polielectrolitos (Acoua tecnologa, 2020).
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Filtracin
    p.mb-5(data-aos='fade-right') Es una operacin en la que se hace pasar el agua a travs de un medio poroso, con el objetivo de retener la mayor cantidad posible de materia en suspensin. El medio poroso tradicionalmente utilizado es un lecho de arena, de altura variable (Acuatecnia S.A.S., 2018), ver figura 18. 
    .row.justify-content-center.mb-5
      .col-lg-10
        .rounded-2.p-5(style="background-color: #dee9f0 ")
          .row.justify-content-center.mb-5
            .col-lg-8
              .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                h5 Figura 18 
                br
                span.fst-italic Filtracin 
              img(src='@/assets/curso/temas/tema2/29.jpg', alt='')
              figcaption Nota. Tomado de Pedraza (2017). Filtracin de aguas residuales
    TabsC.color-acento-contenido.tabs-c-editada.mb-5
      .py-4.py-md-5.px-4(titulo="Operacin unitaria" style="background-color: #f3f9ff")
        .row
          .col-md-8.mb-4.mb-md-0
            p La filtracin como operacin unitaria para los procesos de tratamiento de aguas residuales es una prctica relativamente reciente. Actualmente la filtracin se emplea de modo generalizado para conseguir una mayor eliminacin de slidos en suspensin, de los efluentes de los procesos de tratamiento biolgicos y qumicos, tambin se utiliza para la eliminacin del fsforo precipitado por va qumica, y como etapa previa de un proceso de electrodilisis (Otero, 2006).  
          .col-md-4
            img(src='@/assets/curso/temas/tema2/30.png', alt='')
      .py-4.py-md-5.px-4(titulo="Formas de aplicacin de la filtracin " style="background-color: #f3f9ff")
        .row
          .col-md-8.mb-4.mb-md-0
            p La filtracin puede efectuarse de muchas formas, con baja carga superficial (filtros lentos) o con alta carga superficial (filtros rpidos), en medios porosos (pastas arcillosas, papel de filtro) o en medios granulares (arena, antracita, granate o combinados), con flujo ascendente, descendente y mixto (parte ascendente y parte descendente). Por otro lado, el filtro puede trabajar a presin o por gravedad, segn sea la magnitud de la carga hidrulica que exista sobre el lecho filtrante (Otero, 2006). 
          .col-md-4
            img(src='@/assets/curso/temas/tema2/31.png', alt='')
      .py-4.py-md-5.px-4(titulo="Clasificacin de los filtros" style="background-color: #f3f9ff")
        .row.justify-content-center
          .col-lg-12.mb-4.mb-md-0
            p Los filtros pueden clasificarse de la siguiente manera, ver tabla 2. 
          .col-lg-10
