    Separador
    #t_2_3.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.3 Tratamientos secundarios del agua residual 
    .row.justify-content-center.mb-5
      .col-lg-5.col-8.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/tema2/33.jpg', alt='')
      .col-lg-7(data-aos="fade-left")
        p El tratamiento secundario de depuracin constituye una serie de importantes procesos de naturaleza biolgica de tratamiento de las aguas residuales que tienen en comn la utilizacin de microorganismos (entre las que destacan las bacterias) para llevar a cabo la eliminacin de materia orgnica biodegradable, tanto coloidal como disuelta, as como la eliminacin de compuestos que contienen elementos nutrientes (N y P) (Cyclusid depuracin industrial, s.f.). 
          br 
          br 
          |En la mayor parte de los casos la materia orgnica constituye la fuente de energa y de carbono que necesitan los microorganismos para su crecimiento. Adems, tambin es necesaria la presencia de nutrientes que contengan los elementos esenciales para el crecimiento, especialmente los compuestos que contengan N y P, y por ltimo, en el caso de sistema aerobio, la presencia de oxgeno disuelto en el agua. Este ltimo aspecto ser clave a la hora de elegir el proceso biolgico ms conveniente (Cyclusid depuracin industrial, s.f.).
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Digestor aerbico
    .row.justify-content-center.mb-5
      .col-lg-7.mb-lg-0.mb-3
        p Es un mtodo alternativo de tratar los lodos orgnicos producidos en el curso de las diversas operaciones de tratamiento. Los digestores aerbicos se pueden emplear para el tratamiento de: (1) nicamente lodos activados o de filtros percoladores; (2) mezclas de lodos activados o de filtros percoladores con lodos primarios, o (3) lodo biolgico en exceso de plantas de tratamiento de lodos activados sin sedimentacin primaria. Actualmente, suelen emplearse dos variantes del proceso de digestin aerobia: el sistema convencional y el sistema con oxgeno puro, aunque tambin se ha empleado la digestin aerobia termfila (Aguamarket, 2017), ver figura 20. 
      .col-lg-5.col-8
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 20
          br
          span.fst-italic Tratamiento aerobio
        img(src='@/assets/curso/temas/tema2/34.png', alt='')
        figcaption Nota. Tomado de Global Water & Energy (2020). http://www.globalwe.com/es/soluciones/aguas-residuales/tratamiento-aerobio/ 
    .bg-1.p-5.mb-5.mn
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-5
          h4 Digestin aerbica convencional
          p En esta el lodo se airea durante un largo perodo de tiempo en un estanque abierto, sin calefaccin, empleando difusores convencionales o aireadores superficiales. El proceso se puede llevar a cabo de manera continua o discontinua. En plantas de pequeo tamao se emplea el sistema discontinuo, en el que el lodo se airea y se mezcla completamente durante un largo perodo de tiempo, dejndose sedimentar a continuacin en el interior del mismo estanque (Aguamarket, 2017), ver figura 21.
          .d-lg-none.d-block.mb-4
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 21
              br
              span.fst-italic  Tanque aerobio TAE  
            img(src='@/assets/curso/temas/tema2/35.jpg', alt='')
            figcaption Nota. Tomado de Ajover (s.f.). http://www.ajover.co/wp-content/uploads/2019/02/fichatecnicasistemasepticoecoajoverajover.pdf
          h4 Digestin con oxgeno y digestin aerbica termfila 
          p En los sistemas continuos, la decantacin y concentracin del lodo se realiza en un estanque independiente. La digestin con oxgeno puro es una modificacin del proceso de digestin aerobia en el que se sustituye el aire por oxgeno puro. El lodo que resulta es parecido al lodo que se obtiene en los procesos de digestin aerobia convencionales. La digestin aerobia termfila representa un refinamiento adicional del proceso de digestin aerobia. Este proceso puede permitir conseguir altos rendimientos de eliminacin de la fraccin biodegradable (superiores al 80%) en tiempos de detencin cortos (3 a 4 das), mediante la accin de bacterias termfilas a temperaturas entre 25 y 50 C superiores a la temperatura ambiente (Aguamarket, 2017). 
        .col-lg-7.col-8
          .d-lg-block.d-none
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 21
              br
              span.fst-italic  Tanque aerobio TAE  
            img(src='@/assets/curso/temas/tema2/35.jpg', alt='')
            figcaption Nota. Tomado de Ajover (s.f.). http://www.ajover.co/wp-content/uploads/2019/02/fichatecnicasistemasepticoecoajoverajover.pdf
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Digestor anaerobio
    .row.justify-content-center.mb-5
      .col-lg-6(data-aos="fade-right").mb-lg-0.mb-3
        p En este proceso se produce la descomposicin de la materia orgnica e inorgnica en ausencia de oxgeno molecular. Sus principales aplicaciones han sido y siguen siendo hoy en da, la estabilizacin de lodos concentrados producidos en el tratamiento del agua residual y de determinados residuos industriales; sin embargo, recientemente se ha demostrado que los residuos orgnicos diluidos tambin se pueden tratar anaerbicamente. En el proceso de digestin anaerobia, la materia orgnica contenida en la mezcla de lodos primarios y biolgicos se convierte biolgicamente, bajo condiciones anaerobias, en metano (CH4) y dixido de carbono (C02) (Aguamarket, 2017), ver figura 22
      .col-lg-6.col-8(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 22
          br
          span.fst-italic  Digestin anaerobia 
        img(src='@/assets/curso/temas/tema2/36.png', alt='')
        figcaption Nota.  Tomado de Aguas residuales info (2014). https://www.aguasresiduales.info/revista/blog/digestion-anaerobia-de-fangos-de-edar-problemas-y-soluciones  
    TabsB.color-acento-contenido.mb-5
      .py-4.py-md-5(titulo="Dnde sucede el proceso? " :icono="require('@/assets/curso/temas/tema2/37.svg')")
        .row
          .col-lg-6.mb-4.mb-md-0
            p El proceso se lleva a cabo en un reactor completamente cerrado. Los lodos se introducen en el reactor de forma continua o intermitente, y permanecen en su interior durante perodos de tiempo variables. El lodo estabilizado, que se extrae del proceso continuo o intermitentemente, tiene un bajo contenido en materia orgnica y patgenos, y no es putrescible. Los dos tipos de digestores anaerbicos ms empleados son los de alta y baja carga. En el proceso de digestin de baja carga no se suele calentar, ni mezclar el contenido del digestor, y los tiempos de detencin oscilan entre 30 y 60 das. En los procesos de digestin de alta carga el contenido del digestor se calienta y mezcla completamente (Aguamarket, 2017), ver figura 23.  
          .col-lg-6
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 23
              br
              span.fst-italic  Digestor anaerobio para lodos
            img(src='@/assets/curso/temas/tema2/39.png', alt='')
            figcaption Nota.  Tomado de Equimar (s.f.). http://equimar.mx/our-services/digestores-anaerobios-para-lodos/   
      .py-4.py-md-5(titulo="Etapas de la conversin biolgica " :icono="require('@/assets/curso/temas/tema2/37.svg')")
        .row
          .col-lg-6.mb-4.mb-md-0
            p La conversin biolgica de la materia orgnica de los lodos parece que se produce en tres etapas. El primer paso del proceso comporta la transformacin por va enzimtica (hidrlisis) de los compuestos de alto peso molecular en compuestos que puedan servir como fuentes de energa y de carbono celular. El segundo paso (acidognesis) implica la conversin bacteriana de los compuestos producidos en la primera etapa en compuestos intermedios identificables de menor peso molecular. El tercer paso (metanognesis) supone la conversin bacteriana de los compuestos intermedios en productos finales ms simples, principalmente metano y dixido de carbono. En un digestor, la conversin de los lodos orgnicos y de los residuos se lleva a cabo mediante la accin conjunta de diferentes organismos anaerobios (Aguamarket, 2017). 
          .col-lg-6
            img(src='@/assets/curso/temas/tema2/40.png', alt='')
      .py-4.py-md-5(titulo="Hidrolizacin" :icono="require('@/assets/curso/temas/tema2/37.svg')")
        .row
          .col-lg-6.mb-4.mb-md-0
            p Un grupo de microorganismos se ocupa de la hidrolizacin de los polmeros orgnicos y de los lpidos para formar elementos estructurales bsicos como los monosacridos, los aminocidos y los compuestos relacionados con estos. Un segundo grupo de bacterias anaerobias fermenta los productos de la descomposicin para producir cidos orgnicos simples, de los que el que se presenta con mayor frecuencia en los digestores orgnicos es el cido actico. Este grupo de microorganismos, que reciben el nombre de no metanognicos, est formado por bacterias facultativas y anaerobias estrictas, aunque de forma colectiva se conocen como bacterias "formadoras de cidos (Aguamarket, 2017). 
          .col-lg-6
            img(src='@/assets/curso/temas/tema2/41.png', alt='')
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Lechos bacterianos o filtros percoladores
    p.mb-5(data-aos='fade-right') Se trata de un sistema de depuracin biolgico aerobio cuyo funcionamiento se basa en hacer circular a travs de un medio poroso, aire y agua residual. Estn configurados en forma de cultivo fijo no sumergido y compuestos por un lecho, normalmente de gran dimetro, que puede estar formado por materiales naturales cantos rodados, coque metalrgico, escoria o antracita- o plsticos, y en cuya superficie se aplican de forma uniforme los lquidos residuales por medio de pulverizadores, y en forma de lluvia (SMA, 2015), ver figuras 24 y 25.
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 24
          br
          span.fst-italic Filtro percolador 
        img(src='@/assets/curso/temas/tema2/42.png', alt='')
        figcaption Nota. Tomado de Mata (s.f.).  https://lorenzomata95.wixsite.com/misitio/
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 25
          br
          span.fst-italic  Filtro percolador
        img(src='@/assets/curso/temas/tema2/43.png', alt='')
        figcaption Nota. Tomado de filtropercolador (2019) https://filtropercolador.blogspot.com/2019/03/1.html 
    p.mb-5(data-aos='fade-right') En la parte inferior del lecho se sitan el sistema de drenaje para la salida del efluente del proceso y el sistema de entrada de aire para la ventilacin. Esta puede llevarse a cabo de forma natural, lo que exige para un correcto funcionamiento diferencias de temperatura superiores a 2C entre el aire -agua o forzada, suele emplearse solo cuando el tamao del filtro es demasiado grande y no se puede garantizar la ventilacin natural. Cabe destacar que el lecho debe permanecer aireado y con un caudal suficiente de percolacin, que permita arrastrar las fracciones de biopelcula erosionadas o desprendidas, y sin saturacin de agua. De esta forma, se puede prevenir, en caso de cargas altas, la formacin de charcos en su superficie (SMA, 2015), ver figura 26. 
    .row.justify-content-center.mb-5
      .col-lg-10
        .rounded-2.p-5(style="background-color: #dee9f0 ")
          .row.justify-content-center.mb-5
            .col-lg-8
              .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                h5 Figura 26
                br
                span.fst-italic Partes de un filtro percolador
              img(src='@/assets/curso/temas/tema2/44.jpg', alt='')
              figcaption Nota. Tomado de filtropercolador (2019). https://filtropercolador.blogspot.com/2019/03/1.html  
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Contactores biolgicos rotativos: biodiscos y biocilindros
    p.mb-5(data-aos='fade-right') Los contactores biolgicos rotativos estn basados en los procesos biolgicos aerobios. Estos requieren un contacto ntimo entre el agua residual, la biomasa activa y el oxgeno. Consisten en una serie de placas o discos colocados en un eje horizontal que giran lentamente dentro del tanque que contiene el agua residual, con un 49% de la superficie sumergida. Sobre la superficie de los discos se fija la biomasa que se encuentra alternativamente en contacto con el agua residual y el aire. Esto posibilita la ingestin de la materia orgnica por parte de los microorganismos y su posterior degradacin en presencia del oxgeno del aire (Cyclusid, 2020), ver figura 27.  
    .titulo-sexto.color-acento-contenido(data-aos='fade-right')
      h5 Figura 27
      br 
      span.fst-italic Unidad de tratamiento por biodiscos
    .row.justify-content-center.mb-4
      .col-lg-6(data-aos="fade-right").mb-lg-0.mb-3: img(src='@/assets/curso/temas/tema2/45.jpg', alt='')
      .col-lg-6(data-aos="fade-right"): img(src='@/assets/curso/temas/tema2/46.jpg', alt='')
      figcaption Nota. Tomado de Pedraza (2018). Unidad de tratamiento por biodiscos - feria FIMA. 
    .bg-1.mn.p-5.mb-5
      .row.justify-content-center.mb-5
        .col-lg-6.mb-lg-0.mb-3
          h4 Los biocilindros
          p Son una variante de los biodiscos, su principal diferencia es la sustitucin de los discos por biocilindros, estn formados por una malla metlica, cuyo interior se rellena de piezas de plstico que sirven de soporte a la biomasa. En estos sistemas se produce un exceso de biomasa que se desprende del contactor, esta se mantiene en suspensin en el tanque gracias al movimiento del material soporte. Para regular la cantidad de microorganismos presentes en el sistema se dispone de un clarificador o decantador secundario, en l se produce la sedimentacin del exceso de biomasa (Cyclusid, 2020), ver figura 28. 
          .d-lg-none.d-block.mb-4
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 28
              br
              span.fst-italic  Esquema del sistema de biodiscos 
            img(src='@/assets/curso/temas/tema2/47.jpg', alt='')
            figcaption Nota. Tomado de Delgado (2018). http://revistas.uni.edu.pe/index.php/tecnia/article/view/545/1076  
          h4 Caractersticas notables del proceso con biodiscos 
          p Notable elasticidad, es decir, es capaz de absorber elevadas puntas de carga orgnica e hidrulica, manteniendo muy estables los niveles de salida. No requiere recirculacin de fangos; el fango activo del proceso es el que permanece adherido a los discos. Si la carga aumenta, la pelcula bacteriana crece espontneamente y los slidos en suspensin a la salida del biodisco disminuyen. Si la carga disminuye, la biomasa se desprende y aumentan los slidos en el efluente al decantador secundario. No es necesario controlar la concentracin del licor de mezcla, ya que el fango extrado es un desecho del proceso y ha de eliminarse como mejor convenga. 
        .col-lg-6(data-aos="fade-left")
          .d-lg-block.d-none
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 28
              br
              span.fst-italic  Esquema del sistema de biodiscos 
            img(src='@/assets/curso/temas/tema2/47.jpg', alt='')
            figcaption Nota. Tomado de Delgado (2018). http://revistas.uni.edu.pe/index.php/tecnia/article/view/545/1076  
      h4 Ruidos y otras variables 
      p.mb-5(data-aos='fade-right') El nivel de ruidos es muy bajo, factor que es muy importante tanto ambientalmente como por higiene y salud del personal. Las dimensiones de los depsitos de oxidacin son menores que los utilizados por otros procesos. El rendimiento del proceso es ms estable en pocas fras, debido al hecho de permanecer cubiertos los tanques. El consumo energtico es muy reducido y a igualdad de resultados, comparndolo con un sistema de fangos activos, el consumo resulta ser la tercera parte Hidrometalica (s.f.). 
      h4 El funcionamiento de los biodiscos
      p Se divide en cuatro etapas, ver figura 29. 
      .row.justify-content-center.mb-5
        .col-lg-10
          .rounded-2.p-5(style="background-color: #dee9f0 ")
            .row.justify-content-center.mb-5
              .col-lg-8
                .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                  h5 Figura 29
                  br
                  span.fst-italic Biodiscos
                img(src='@/assets/curso/temas/tema2/48.jpg', alt='')
                figcaption Nota. Tomado de Hidrometalica (s.f.). https://hidrometalica.com/biodiscos/  
      h4 El agua bruta 
      p.mb-0(data-aos='fade-right') El agua bruta de llegada entra a este tanque a travs de la tubuladura de entrada, donde se produce una primera sedimentacin de materia slida. Los slidos sedimentados se extraern peridicamente por parte del usuario en la primera etapa donde se realiza la primera toma de contacto entre el agua bruta de entrada y los discos. La superficie de los discos se coloniza de forma natural, formando un recubrimiento visible. En la segunda etapa de contacto, gracias a la concepcin del sistema de vertedero entre ambas etapas, se consigue un nivel de agua con menos fluctuaciones, favoreciendo el proceso y su salida hacia el tanque de sedimentacin secundaria. Con el proceso de dos etapas se consigue aumentar el crecimiento de la biomasa de forma equilibrada y constante. En el tanque de sedimentacin secundaria se produce la sedimentacin de la materia slida restante del proceso, a la cual se le realizan dos operaciones, la recirculacin y extraccin de sedimentos (Hidrometlica, s.f.). 
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Reactores biolgicos secuenciales (SBR)
    p.mb-5(data-aos='fade-right') Son reactores discontinuos en los que el agua residual se mezcla con un lodo biolgico en un medio aireado. Se trata de un proceso que se combina en un mismo tanque: aeracin/reaccin y clarificacin. La tecnologa de los reactores biolgicos secuenciales es una variante optimizada de la tecnologa convencional de lodos activados. Se basa en el uso de un solo reactor que opera en forma discontinua secuencial. El sistema de los reactores biolgicos secuenciales SBR consta de al menos cuatro procesos cclicos: llenado, aireacin, anoxia, decantacin y vaciado, tanto de efluente como de los lodos. Esta tecnologa es capaz de tolerar variaciones de carga y caudal y genera como producto lodos estabilizados, siendo en ocasiones, la tecnologa ms apropiada para la industria, ver figura 30 y 31. 
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 30
          br
          span.fst-italic Instalacin con tecnologa SBR 
        img(src='@/assets/curso/temas/tema2/49.png', alt='')
        figcaption Nota. Tomado de Aguas industriales (2015). Instalacin con tecnologa SBR.
      .col-lg-6(data-aos="fade-right")
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 31
          br
          span.fst-italic Planta de tratamiento de aguas residuales reactor biolgico secuencial (SBR)  
        img(src='@/assets/curso/temas/tema2/50.png', alt='')
        figcaption Nota. Tomado de Gv soluciones (s.f.). https://studylib.es/doc/4574213/planta-de-tratamiento-de-aguas-residuales-reactor-biol%C3%B3gico  
    p.mb-5(data-aos='fade-right') Se debe considerar que el tratamiento de aguas residuales mediante SBR se lleva a cabo en secuencia en 4 etapas: 
    .row.justify-content-center.mb-5
      .col-lg-3.mb-lg-0.mb-3(data-aos="fade-right")
        .p-5.px-3.text-center.h-100(style="background-color: #dee9f0; border-bottom: 5px solid #badc61 ") 
          img(src='@/assets/curso/temas/tema2/51.svg', alt='' style='width:58px').m-auto.mb-3
          p #[b Etapa de llenado:] es cuando se aade el sustrato al reactor. Segn los objetivos previstos esta etapa puede ser esttica, mezclada o aireada.
      .col-lg-3.mb-lg-0.mb-3(data-aos="fade-right")
        .p-5.px-3.text-center.h-100(style="background-color: #dee9f0; border-bottom: 5px solid #badc61 ") 
          img(src='@/assets/curso/temas/tema2/52.svg', alt='' style='width:58px').m-auto.mb-3
          p #[b Etapa de reaccin:] las necesidades del tratamiento dictarn si el reactor se somete o no a aireacin. Aqu se proveen condiciones de mezcla en las que se permite el consumo de sustrato en condiciones ambientales controladas. 
      .col-lg-3.mb-lg-0.mb-3(data-aos="fade-right")
        .p-5.px-3.text-center.h-100(style="background-color: #dee9f0; border-bottom: 5px solid #badc61 ") 
          img(src='@/assets/curso/temas/tema2/53.svg', alt='' style='width:58px').m-auto.mb-3
          p #[b Sedimentacin:] permite la separacin de slidos en condiciones de quietud. El resultado es un sobrenadante clarificado que se puede descargar como efluente. Generalmente, el tiempo de asentamiento vara entre media hora y una hora y media. Este proceso previene que el manto de slidos flote, debido a la acumulacin de gas. 
      .col-lg-3.mb-lg-0.mb-3(data-aos="fade-right")
        .p-5.px-3.text-center.h-100(style="background-color: #dee9f0; border-bottom: 5px solid #badc61 ") 
          img(src='@/assets/curso/temas/tema2/54.svg', alt='' style='width:58px').m-auto.mb-3
          p #[b Vaciado:] extraccin del agua clarificada del reactor. El exceso de lodo activado residual es removido; este proceso toma tiempo, puede tardar entre un 5% y un 30% del tiempo total (Domos agua, 2019). 
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Filtros verdes y humedales artificiales
    p Un filtro verde consiste en una superficie de terreno donde se establece una plantacin forestal o agrcola, a la que se suministra, de forma recurrente y programada, aguas residuales urbanas. Una parte de estas aguas se evapora a la atmsfera y el resto es absorbido por las races de las plantas o es filtrada a travs del suelo (TECPA, 2018), ver figura 32.
    .row.justify-content-center.mb-5
      .col-lg-4.col-8.mb-lg-0.mb-3
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Figura 32
            br
            span.fst-italic Filtros verdes en depuracin de aguas residuales. 
        img(src='@/assets/curso/temas/tema2/59.png', alt='')
        figcaption Nota. Tomado de TECPA (2018). https://www.tecpa.es/filtro-verde-depuracion-aguas-residuales
      .col-lg-8.mb-lg-0.mb-3(data-aos="fade-right")
        h4 Filtros verdes
        p Un filtro verde provoca procesos fsicos, qumicos y biolgicos naturales en el ecosistema suelo-agua. Estos procesos naturales reducen de forma considerable gran parte de los contaminantes del agua residual como: demanda biolgica de oxgeno (DBO5), demanda qumica de oxgeno (DQO), slidos suspendidos, nitrgeno, fsforo, y microorganismos patgenos (TECPA, 2018), ver figura 33.
        .row.justify-content-center.mb-5
          .col-lg-10 
            .titulo-sexto.color-acento-contenido(data-aos='fade-right')
              h5 Figura 33
              br
              span.fst-italic Tratamiento de aguas residuales mediante humedales artificiales 
            img(src='@/assets/curso/temas/tema2/57.png', alt='')
            figcaption Nota. Tomado de Construyored (2018). https://construyored.com/oportunidades/6216-tratamiento-de-aguas-residuales-mediante-humedales-artificiales-el-sistema-frances-i  
    .mn(style="background-color: #fdf8e5 ").p-5.mb-4
      .row.justify-content-center.mb-5
        .col-lg-5.mb-lg-0.mb-3
          h4 Depuracin de los filtros verdes 
          p De acuerdo con la Universidad Veracruzana los mecanismos de depuracin de los filtros verdes son, en primera instancia, los procesos fsicos: granulometra y textura. Luego, procesos qumicos: pH, capacidad de intercambio inico, condiciones de aireacin y encharcamiento. En tercer lugar, procesos biolgicos: races, microorganismos (Universidad Veracruzana, s.f.), ver figura 34.
        .col-lg-7
          .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Figura 34
            br
            span.fst-italic Bioingeniera
          img(src='@/assets/curso/temas/tema2/58.png', alt='')
          figcaption https://encrypted-tbn2.gstatic.com/images?q=tbn:ANd9GcRYHJik2RkWb3hB5ts5mpu-G6UMEJAdXAETR1C7J674yIcA2qxB  
    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0  Reactor biolgico de membrana (MBR)
    .row.justify-content-center.mb-5
      .col-lg-5.mb-lg-0.mb-3(data-aos="fade-right")
        p #[b (Reactor biolgico + ultrafiltracin)] se incluyen en las denominadas tecnologas de membrana, las cuales han experimentado un gran desarrollo en la ltima dcada. La aplicacin de estas tecnologas a los MBR permite la separacin del fango y el lquido mediante membranas, obteniendo ventajas importantes frente a la separacin en los tradicionales decantadores secundarios. El aumento de la demanda de agua ha impulsado la implantacin de estos sistemas a escala real, especialmente en aquellos casos en que se plantea la posibilidad de reutilizacin de agua (Universidad de Alcal, 2007), ver figura 35. 
      .col-lg-7.col-8
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5 Figura 35
          br
          span.fst-italic  Biorreactor de membrana
        img(src='@/assets/curso/temas/tema2/61.png', alt='')
        figcaption Nota. Tomado de Bioazul (s.f). https://www.bioazul.com/mbr-biorreactor-de-membrana/  
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-3
        h4 Agua filtrada por membrana
        p Su funcionamiento se basa en que el agua del reactor biolgico es filtrada pasando a travs de las paredes de una membrana, debido a una pequea depresin producida por una bomba centrfuga. El agua filtrada es extrada del sistema mientras el fango y los compuestos de tamao superior al poro de la membrana quedan retenidos y permanecen o retornan al reactor biolgico (Universidad de Alcal, 2007). 
      .col-lg-6
        h4 Contralavado
        p Este ciclo se alterna con un corto contralavado, en el que se invierte el sentido del flujo para forzar el paso del agua filtrada desde el interior al exterior de la membrana para limpiarla. Peridicamente, en funcin del grado de ensuciamiento se realizan limpiezas qumicas en profundidad de las membranas mediante su inmersin en una solucin de limpieza (Universidad de Alcal, 2007). 
    .p-5.px-1(style="background-color: #d9e2f4").mb-5
      .row.justify-content-center
        .col-lg-3
          h4 MBR y sus partes 
          p De acuerdo con Remtamavares, los MBR estn compuestos por dos partes principales: 
        .col-lg-4.mb-lg-0.mb-3
          .card-icon.h-100 
            .card-icon__icon: img(src='@/assets/curso/temas/tema2/62.svg', alt='')
            .card-icon__info
              p.mb-0 - #[b Reactor biolgico:] responsable de la degradacin de los compuestos presentes en el agua residual.
        .col-lg-4
          .card-icon.h-100 
            .card-icon__icon: img(src='@/assets/curso/temas/tema2/63.svg', alt='')
            .card-icon__info
              p.mb-0 - #[b Mdulo de membranas:] encargado de llevar a cabo la separacin fsica del licor de mezcla (Universidad de Alcal, 2007).
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
        h4 Configuraciones bsicas
        p Membranas o sistemas sumergidos: las membranas se sitan dentro del propio reactor biolgico, eliminando las necesidades de bombeo y aprovechando la agitacin mecnica de la aireacin (Universidad de Alcal, 2007), ver figura 36.
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
          h5  Figura 36
