        h4 Membranas externas o sistema de bucle externo 
        p El contenido del reactor biolgico se bombea al mdulo de membranas. Las ventajas de este modelo residen en que el propio mdulo de membranas sirve de contenedor de limpieza para las mismas y se evita su manipulacin (Universidad de Alcal, 2007), ver figura 37. 
