    .tarjeta.color-primario.rounded-pill.d-inline-block.m-auto.p-3.mb-4
      h4.mb-0 Electrocoagulacin
    p El tratamiento por electrocoagulacin en su esencia es similar a un proceso fisicoqumico de coagulacin-floculacin en el que es necesario aadir una serie de productos qumicos, basados normalmente en sales metlicas para conseguir el proceso de coagulacin, es decir, de desestabilizacin de los coloides responsables de la materia en suspensin (Hidritec, s.f.), ver figura 38. 
    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Figura 38
            br 
            span.fst-italic Electrocoagulacin
        img(src='@/assets/curso/temas/tema2/67.png', alt='')
        figcaption.mb-4 Nota. Tomado de Hidritec (s.f.). https://encrypted-tbn2.gstatic.com/images?q=tbn:ANd9GcQumrPqtcLRY_AE6MBByyMuT-oDISLdsCV43JswIXr2FLM3ewSN  
    .row.justify-content-center.mb-5
      .col-lg-12(data-aos="fade-right")
        .tarjeta.p-4(style="background-color: #fdf8e4 ")
          h4 Desestabilizacin de los coloides 
          p.mb-5  Mediante el procedimiento de electrocoagulacin se aplica una diferencia de potencial a unos electrodos metlicos (normalmente aluminio y/o hierro). De esta forma, se produce una rpida desestabilizacin de los coloides por aplicacin elctrica y disolucin de iones metlicos de los electrodos empleados. 
          h4 Desinfeccin de bacterias, virus y parsitos 
          p La consecuencia es una importante reduccin en el contenido en slidos en suspensin y eliminacin de la DQO y DBO5, debido fundamentalmente a la desestabilizacin de los coloides, la decantacin y separacin de materia orgnica, aceites, grasas y metales pesados, as como iones, calcio y magnesio. Por otra parte, tambin se produce la desinfeccin de bacterias, virus o parsitos (Hidritec, s.f.), ver figura 39 un ejemplo grfico de electrocoagulacin. 
          .row.justify-content-center.mb-5
            .col-lg-10
              .titulo-sexto.color-acento-contenido(data-aos='fade-right')
                h5 Figura 39
                br
                span.fst-italic Electrocoagulacin
              img(src='@/assets/curso/temas/tema2/66.svg', alt='')
              figcaption Nota. Tomado de Hidritec (s.f). Electrocoagulacin (Anexos / Figura 46) http://www.hidritec.com/hidritec/electrocoagulacion 
    h4 Eliminacin de slidos, aceites y grasas 
    p.mb-5 El sistema se suministra en una planta premontada con la parrilla de electrodos en forma de placas y dimensionada para el caso concreto, incluyendo el cuadro de control y potencia del equipo. De forma habitual, se suministra con un sistema de decantacin lamelar o separador por flotacin DAF para lograr un mayor rendimiento en la eliminacin de los slidos, aceites y grasas desestabilizados.  
    h4 Biodegradabilidad
    p El sistema es apropiado para aguas residuales de tipo industrial con altas cargas de DQO, slidos en suspensin, grasas, aceites y color como tratamiento nico o bien como pretratamiento biolgico, mejorando la biodegradabilidad del agua residual (Hidritec, s.f.). 
        
    Separador 
    #t_2_4.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.4 Tratamientos terciarios del agua residual 
    p La finalidad de los tratamientos terciarios es eliminar la carga orgnica residual y aquellas otras sustancias contaminantes no eliminadas en los tratamientos secundarios, como por ejemplo, los nutrientes, fsforo y nitrgeno. Estos procesos son de naturaleza biolgica o fisicoqumica, siendo el proceso unitario ms empleado el tratamiento fisicoqumico. Este consta de una coagulacin  floculacin y una decantacin (Cyclusid depuracin industrial, s.f.). 
