      .col-lg-7(data-aos="fade-right")
          p Otros procesos empleados como tratamientos terciarios son las resinas de intercambios de iones, la adsorcin en carbn activo, la ultrafiltracin, la smosis inversa, la electrodesinfeccin, las membranas cermicas, etc. 
